import PictureBrowser from "@/base-ui/picture-browser";
import PropTypes from "prop-types";
import React, { memo, useState } from "react";
import { DetailPictureWrapper } from "./style";

const DetailPicture = memo((props) => {
  const { pictureUrls = [] } = props;
  const [showBrowser, setShowBrowser] = useState(false);
  function viewPhotoHandle() {
    setShowBrowser(true);
  }
  return (
    <DetailPictureWrapper>
      <div className="pictures">
        <div className="left-picture">
          <div className="img-container">
            <img className="imgItem" src={pictureUrls[0]} alt="" />
            <div className="img-cover"></div>
          </div>
        </div>
        <div className="right-picture">
          {pictureUrls.slice(1, 5).map((item) => (
            <div className="img-container" key={item}>
              <img className="imgItem" src={item} alt="" />
              <div className="img-cover"></div>
            </div>
          ))}
        </div>
        <div className="control-btns">
          <div className="top-btns">
            <button>分享</button>
            <button>收藏</button>
          </div>
          <div className="bottom-btns">
            <button className="view-btn" onClick={viewPhotoHandle}>
              查看照片
            </button>
          </div>
        </div>
      </div>
      {showBrowser && (
        <PictureBrowser
          shutdownHandle={(e) => setShowBrowser(false)}
          pictureUrls={pictureUrls}
        />
      )}
    </DetailPictureWrapper>
  );
});

DetailPicture.propTypes = {
  pictureUrls: PropTypes.array,
};

export default DetailPicture;
