import { changeHeaderConfig } from "@/store/modules/main";
import React, { memo, useEffect } from "react";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import DetailInfos from "./childrens/detail-infos";
import DetailPicture from "./childrens/detail-picture";

import { DetailWrapper } from "./style";

const Detail = memo(() => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(changeHeaderConfig({ isFixed: false }));
  }, [dispatch]);
  const { roomDetailInfo } = useSelector((state) => state.detail, shallowEqual);
  return (
    <DetailWrapper>
      <DetailPicture pictureUrls={roomDetailInfo.picture_urls} />
      <DetailInfos />
    </DetailWrapper>
  );
});

export default Detail;
