import PropTypes from "prop-types";
import React, { memo, useEffect, useState, useRef } from "react";
import { CSSTransition, SwitchTransition } from "react-transition-group";

import IconArrowLeft from "@/assets/svg/icon-arrow-left";
import IconArrowRight from "@/assets/svg/icon-arrow-right";
import { IconShutdown } from "@/assets/svg/icon_shutdown";
import { PicBrowserWrapper } from "./style";
import Indicator from "../indicator";
import IconHidden from "@/assets/svg/icon_hidden";
import classNames from "classnames";

const PictureBrowser = memo((props) => {
  const { shutdownHandle, pictureUrls } = props;
  const [selectIndex, setSelectIndex] = useState(0);
  const [showIndicator, setShowIndicator] = useState(true);
  function indicatorClick(index) {
    setSelectIndex(index);
  }
  function hiddenIndicator() {
    setShowIndicator((state) => !state);
  }
  function nextPicHandle(isNext) {
    let newIndex = selectIndex;
    isNext ? newIndex++ : newIndex--;
    if (newIndex < 0) newIndex = pictureUrls.length - 1;
    if (newIndex > pictureUrls.length - 1) newIndex = 0;
    setSelectIndex(newIndex);
  }
  useEffect(() => {
    document.body.style.overflow = "hidden";
    return () => {
      document.body.style.overflow = "auto";
    };
  });
  return (
    <PicBrowserWrapper showIndicator={showIndicator}>
      <div className="top-section">
        <div className="shutdown" onClick={shutdownHandle}>
          <IconShutdown />
        </div>
      </div>
      <div className="middle-section">
        <div className="control">
          <div className="left" onClick={(e) => nextPicHandle(false)}>
            <IconArrowLeft size={{ height: 65, width: 65 }} />
          </div>
          <div className="right" onClick={(e) => nextPicHandle(true)}>
            <IconArrowRight size={{ height: 65, width: 65 }} />
          </div>
        </div>
        <div className="pic-browser">
          <SwitchTransition mode="in-out">
            <CSSTransition
              key={pictureUrls[selectIndex]}
              classNames="fade"
              timeout={200}
            >
              <img src={pictureUrls[selectIndex]} alt="" />
            </CSSTransition>
          </SwitchTransition>
        </div>
      </div>
      <div className="bottom-section">
        <div className="preview">
          <div className="info">
            <div className="leftInfo">
              {selectIndex + 1}/{pictureUrls.length}:room Apartment图片
              {selectIndex + 1}
            </div>
            <div className="rightInfo" onClick={hiddenIndicator}>
              {showIndicator ? "隐藏" : "显示"}照片列表 <IconHidden />
            </div>
          </div>
          <Indicator index={selectIndex}>
            {pictureUrls.map((item, index) => (
              <div
                key={item}
                className="item-container"
                onClick={(e) => indicatorClick(index)}
              >
                <img
                  className={classNames("item", {
                    active: selectIndex === index,
                  })}
                  src={item}
                  alt=""
                />
              </div>
            ))}
          </Indicator>
        </div>
      </div>
    </PicBrowserWrapper>
  );
});

PictureBrowser.propTypes = {
  pictureUrls: PropTypes.array,
};

export default PictureBrowser;
