import styled from "styled-components";

export const DetailPictureWrapper = styled.div`
  > .pictures {
    height: 600px;
    display: flex;
    position: relative;
    &:hover {
      .img-cover {
        opacity: 1 !important;
      }
      .img-cover {
        &:hover {
          opacity: 0 !important;
        }
      }
    }
  }
  .left-picture,
  .right-picture {
    width: 50%;
    height: 100%;
    .img-container {
      border: 1px solid #000;
      box-sizing: border-box;
      position: relative;
      overflow: hidden;
      &:hover {
        .imgItem {
          transform: scale(1.1);
        }
      }
      .imgItem {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 300ms ease;
      }
      .img-cover {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.6);
        opacity: 0;
        transition: opacity 150ms ease-in;
      }
    }
  }
  .left-picture {
    .img-container {
      height: 100%;
      width: 100%;
    }
  }

  .right-picture {
    display: flex;
    flex-wrap: wrap;
    .img-container {
      height: 50%;
      width: 50%;
    }
  }
  .control-btns {
    position: absolute;
    right: 25px;
    bottom: 35px;
    top: 35px;
    display: flex;
    flex-flow: column;
    justify-content: space-between;

    button {
      padding: 7px 0;
      width: 90px;
      margin-left: 30px;
      border-radius: 7px;
      border: 0.5px solid #000;
      font-weight: 600;
      color: #484848;
    }
    .view-btn {
      float: right;
    }
  }
`;
