import { height } from "@mui/system";
import styled from "styled-components";
export const PicBrowserWrapper = styled.div`
  display: flex;
  flex-flow: column;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 999;
  background-color: #484848;
  .top-section {
    height: 120px;
    .shutdown {
      position: absolute;
      top: 10px;
      right: 20px;
      color: #fff;
      cursor: pointer;
    }
  }
  .middle-section {
    flex: 1;
    position: relative;
    display: flex;
    justify-content: center;
    .control {
      position: absolute;
      bottom: 50%;
      left: 15px;
      right: 15px;
      display: flex;
      justify-content: space-between;
      .left,
      .right {
        color: #fff;
        cursor: pointer;
      }
    }
    .pic-browser {
      width: 100%;
      height: 100%;
      max-width: 105vh;
      overflow: hidden;
      position: relative;
      img {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        margin: 0 auto;
        user-select: none;
      }
      .fade-enter {
        transform: translateX(100%);
        opacity: 0;
      }
      .fade-enter-active {
        transform: translate(0);
        opacity: 1;
        transition: all 200ms ease;
      }
      .fade-exit-active {
        opacity: 0;
        transition: all 200ms ease;
      }
    }
  }
  .bottom-section {
    height: 106px;
    width: 900px;
    margin: 0 auto;
    margin-bottom: 50px;
    .info {
      height: 30px;
      color: #fff;
      display: flex;
      justify-content: space-between;
      ${(props) =>
        props.showIndicator ? null : "transform:translateY(76px); "};
      transition: transform 250ms ease;
      .rightInfo {
        cursor: pointer;
      }
    }
    ${(props) => (props.showIndicator ? "76px" : 0)};
    .indicator {
      height: 76px;

      .item-container {
        width: 15%;
        margin-right: 20px;
        position: relative;
        ${(props) =>
          props.showIndicator ? null : "transform:translateY(100%); "};
        transition: transform 250ms ease;
        .item {
          width: 100%;
          height: 100%;
          object-fit: cover;
          opacity: 0.5;
        }
        :last-child {
          margin-right: 0;
        }
        .active {
          opacity: 1;
        }
      }
    }
  }
`;
