import PropTypes from "prop-types";
import React, { memo, useRef, useEffect } from "react";
import { IndicatorWrapper } from "./style";

const Indicator = memo((props) => {
  const { index = 0 } = props;
  const contentRef = useRef();
  useEffect(() => {
    const currentItem = contentRef.current.children[index];
    const currentLeft = currentItem.offsetLeft;
    const currentWidth = currentItem.clientWidth;
    const containerWidth = contentRef.current.clientWidth;
    const maxDistance = contentRef.current.scrollWidth - containerWidth;
    let distance = currentLeft + currentWidth * 0.5 - containerWidth * 0.5;
    if (distance < 0) distance = 0;
    if (distance > maxDistance) distance = maxDistance;
    contentRef.current.style.transform = `translate(${-distance}px)`;
  }, [index]);
  return (
    <IndicatorWrapper className="indicator">
      <div className="indicator-content" ref={contentRef}>
        {props.children}
      </div>
    </IndicatorWrapper>
  );
});

Indicator.propTypes = {
  index: PropTypes.number,
};

export default Indicator;
